/* MOBILE-FIRST OPTIMIZATION - SMARTPHONES PRIORITY */

/* Base mobile styles (default) */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #1f2937 0%, #1e3a8a 50%, #1f2937 100%);
  min-height: 100vh;
  overflow-x: hidden; /* Prevent horizontal scroll on mobile */
  -webkit-tap-highlight-color: transparent; /* Remove tap highlights */
}

/* Streaming SuperBuddy animations */
.streaming-superbuddy {
  display: inline-block;
  filter: drop-shadow(0 4px 8px rgba(0, 212, 255, 0.3));
}

.streaming-superbuddy img {
  border-radius: 50%;
  background: linear-gradient(135deg, #0ea5e9, #06b6d4);
  padding: 8px;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #1f2937;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #06b6d4, #0ea5e9);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #0ea5e9, #06b6d4);
}

/* Text utilities */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Glow effects */
.glow-cyan {
  box-shadow: 0 0 20px rgba(6, 182, 212, 0.5);
}

.glow-blue {
  box-shadow: 0 0 20px rgba(14, 165, 233, 0.5);
}

/* Backdrop blur support */
.backdrop-blur-md {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

/* Gradient text */
.gradient-text {
  background: linear-gradient(135deg, #06b6d4, #0ea5e9, #3b82f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Animation keyframes */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(6, 182, 212, 0.4);
  }
  50% {
    box-shadow: 0 0 30px rgba(6, 182, 212, 0.8);
  }
}

.animate-float {
  animation: float 3s ease-in-out infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}

/* Responsive design improvements */
.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

@media (min-width: 1536px) {
  .container {
    max-width: 1536px;
  }
}

/* Content grid responsive breakpoints - UPDATED FOR PROPER NETFLIX-STYLE SIZING */
.content-grid {
  display: flex;
  gap: 1rem;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 1rem 0;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch; /* iOS smooth scrolling */
  scroll-snap-type: x mandatory;
}

.content-grid::-webkit-scrollbar {
  height: 8px;
}

.content-grid::-webkit-scrollbar-track {
  background: rgba(31, 41, 55, 0.5);
  border-radius: 4px;
}

.content-grid::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #06b6d4, #0ea5e9);
  border-radius: 4px;
}

.content-grid::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #0ea5e9, #3b82f6);
}

/* Content card horizontal layout - PROPER NETFLIX SIZING */
.content-card {
  flex: 0 0 auto;
  width: 240px; /* Proper Netflix-style width */
  max-width: none;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  scroll-snap-align: start;
  border-radius: 8px;
  background: #1f2937;
}

.content-card:hover {
  transform: scale(1.05) translateY(-8px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.4), 0 0 25px rgba(6, 182, 212, 0.3);
  z-index: 10;
  position: relative;
}

.content-card img {
  width: 100%;
  height: 360px; /* Proper poster aspect ratio */
  aspect-ratio: 2/3;
  object-fit: cover;
  border-radius: 8px 8px 0 0;
}

/* Mobile responsive adjustments for horizontal scroll */
@media (max-width: 480px) {
  .content-card {
    width: 140px; /* Mobile: proper readable size */
  }
  
  .content-card img {
    height: 210px; /* Maintain aspect ratio */
  }
  
  .content-grid {
    gap: 0.75rem;
    padding: 0.75rem 0;
  }
}

@media (max-width: 640px) {
  .content-card {
    width: 160px; /* Small screens: slightly larger */
  }
  
  .content-card img {
    height: 240px;
  }
  
  .content-grid {
    gap: 0.875rem;
  }
}

/* Tablet sizing */
@media (min-width: 641px) and (max-width: 1023px) {
  .content-card {
    width: 200px; /* Tablet: medium size */
  }
  
  .content-card img {
    height: 300px;
  }
}

/* Large screens - bigger cards */
@media (min-width: 1280px) {
  .content-card {
    width: 260px; /* Large desktop: bigger cards */
  }
  
  .content-card img {
    height: 390px;
  }
  
  .content-grid {
    gap: 1.25rem;
  }
}

/* Extra large screens */
@media (min-width: 1536px) {
  .content-card {
    width: 280px; /* XL screens: largest cards */
  }
  
  .content-card img {
    height: 420px;
  }
  
  .content-grid {
    gap: 1.5rem;
  }
}

/* Content card content area */
.content-card .card-content {
  padding: 1rem;
}

.content-card .card-title {
  font-size: 0.875rem;
  line-height: 1.25rem;
  margin-bottom: 0.5rem;
}

.content-card .card-platforms {
  margin-bottom: 0.75rem;
}

.content-card .card-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 480px) {
  .content-card .card-content {
    padding: 0.75rem;
  }
  
  .content-card .card-title {
    font-size: 0.75rem;
    line-height: 1rem;
  }
  
  .content-card .card-actions button {
    padding: 0.375rem 0.75rem;
    font-size: 0.75rem;
  }
}

/* Category section spacing - IMPROVED */
.category-section {
  margin-bottom: 3rem;
}

.category-section h3 {
  margin-bottom: 1.25rem;
  padding-left: 0;
}

/* Container improvements */
.scroll-container {
  position: relative;
  margin: 0 -1rem; /* Extend to edges */
  padding: 0 1rem; /* Add padding back */
}

@media (max-width: 768px) {
  .scroll-container {
    margin: 0 -0.5rem;
    padding: 0 0.5rem;
  }
}

/* Search results - keep grid layout for search */
.search-results-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

@media (max-width: 640px) {
  .search-results-grid {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    gap: 1rem;
  }
}

/* Horizontal scroll indicators */
.scroll-container {
  position: relative;
}

.scroll-indicator {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.7);
  border: none;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0;
}

.scroll-container:hover .scroll-indicator {
  opacity: 1;
}

.scroll-indicator:hover {
  background: rgba(6, 182, 212, 0.8);
  transform: translateY(-50%) scale(1.1);
}

.scroll-indicator.left {
  left: -20px;
}

.scroll-indicator.right {
  right: -20px;
}

@media (max-width: 768px) {
  .scroll-indicator {
    display: none; /* Hide scroll indicators on mobile - touch scroll */
  }
}

/* Header responsive improvements */
.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem;
}

@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    gap: 1rem;
  }
  
  .header-logo {
    order: 1;
  }
  
  .header-search {
    order: 2;
    width: 100%;
    max-width: 100%;
  }
  
  .header-nav {
    order: 3;
    width: 100%;
    justify-content: center;
  }
}

/* Search bar responsive */
.search-container {
  flex: 1;
  max-width: 2xl;
  margin: 0 2rem;
}

@media (max-width: 768px) {
  .search-container {
    margin: 0;
    max-width: 100%;
  }
}

/* Navigation responsive */
.nav-container {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

@media (max-width: 768px) {
  .nav-container {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.75rem;
  }
  
  .nav-container button {
    padding: 0.5rem 0.75rem;
    font-size: 0.875rem;
  }
}

/* Content card responsive */
.content-card {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
}

.content-card img {
  width: 100%;
  height: auto;
  aspect-ratio: 2/3;
  object-fit: cover;
}

@media (max-width: 640px) {
  .content-card {
    margin-bottom: 1rem;
  }
  
  .content-card img {
    height: 240px;
  }
}

/* Button responsive */
.responsive-button {
  padding: 0.75rem 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

@media (max-width: 640px) {
  .responsive-button {
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
    width: 100%;
    text-align: center;
  }
}

/* Modal responsive */
.modal-container {
  width: 100%;
  max-width: 90vw;
  max-height: 90vh;
  margin: auto;
}

@media (max-width: 768px) {
  .modal-container {
    max-width: 95vw;
    max-height: 95vh;
    margin: 0.5rem;
  }
}

/* Settings modal responsive */
.settings-modal {
  display: flex;
  height: 600px;
}

@media (max-width: 768px) {
  .settings-modal {
    flex-direction: column;
    height: auto;
    max-height: 80vh;
  }
  
  .settings-sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #374151;
  }
  
  .settings-content {
    width: 100%;
    max-height: 60vh;
    overflow-y: auto;
  }
}

/* Trailer player responsive */
.settings-modal .settings-tab {
  transition: all 0.2s ease;
}

.settings-modal .settings-tab:hover {
  transform: translateX(4px);
}

.settings-modal .settings-content {
  max-height: 600px;
  overflow-y: auto;
}

.settings-modal .settings-content::-webkit-scrollbar {
  width: 6px;
}

.settings-modal .settings-content::-webkit-scrollbar-track {
  background: #374151;
}

.settings-modal .settings-content::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #06b6d4, #0ea5e9);
  border-radius: 3px;
}

/* Checkbox styling for settings */
.settings-checkbox {
  accent-color: #06b6d4;
}

/* Settings option cards */
.settings-option-card {
  transition: all 0.2s ease;
}

.settings-option-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(6, 182, 212, 0.15);
}

/* Trailer player styles */
.trailer-player {
  width: 100%;
}

.trailer-player .aspect-video {
  aspect-ratio: 16 / 9;
}

.trailer-player iframe {
  border-radius: 0.5rem;
}

/* Responsive video container */
@media (max-width: 768px) {
  .trailer-player {
    margin: 0 -1rem;
  }
}

/* Loading spinner */
.loading-spinner {
  border: 3px solid rgba(6, 182, 212, 0.1);
  border-radius: 50%;
  border-top: 3px solid #06b6d4;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Card hover effects */
.card-hover {
  transition: all 0.3s ease;
}

.card-hover:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3), 0 0 20px rgba(6, 182, 212, 0.2);
}

/* Button styles */
.btn-primary {
  background: linear-gradient(135deg, #06b6d4, #0ea5e9);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #0ea5e9, #3b82f6);
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(6, 182, 212, 0.4);
}

.btn-secondary {
  background: transparent;
  color: #06b6d4;
  border: 2px solid #06b6d4;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
}

.btn-secondary:hover {
  background: #06b6d4;
  color: white;
  transform: translateY(-2px);
}

/* Service badges */
.service-badge {
  background: linear-gradient(135deg, #374151, #4b5563);
  color: #06b6d4;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
  border: 1px solid rgba(6, 182, 212, 0.3);
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  .streaming-superbuddy {
    width: 40px;
    height: 40px;
  }
  
  .container {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

@media (max-width: 640px) {
  .grid-cols-1 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

/* Dark theme enhancements */
.dark {
  color-scheme: dark;
}

/* Focus states for accessibility */
button:focus,
input:focus {
  outline: 2px solid #06b6d4;
  outline-offset: 2px;
}

/* Print styles */
@media print {
  .no-print {
    display: none;
  }
}